const {ccclass, property} = cc._decorator;

import Native from "./NativeController";
import NativeController from "./NativeController";

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Sprite)
    testSprite : cc.Sprite = null;

    onClick(event){
       cc.log(NativeController.instance.selectPhotoFromAlbum());
    }

    onClick2(event){
        cc.loader.load("http://d.lanrentuku.com/down/png/1904/international_food/fried_rice.png", (err, texture)=>{
            this.testSprite.spriteFrame = new cc.SpriteFrame(texture);
        });
    }
}
