const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property({type : cc.Mask, tooltip : "需要刮开的mask"})
    mask : cc.Mask = null;

    @property({tooltip : "绘制点图形的x轴半径"})
    scrapteRadiusX = 30.0;

    @property({tooltip : "绘制点图形的y轴半径"})
    scrapteRadiusY = 30.0;

    @property({tooltip : "需要刮开的图层面积的百分比"})
    scrapteArea = 0.5;

    @property
    pixelNum = 0;

    @property
    achieveNum = 0;

    @property
    pixelPiont : any = null;

    @property({
        type: [cc.Component.EventHandler],
        tooltip: '擦除完成后所触发的事件'
    })
    scrapeEvents = [];

    onLoad() {
        this.init();
        (window as any).SCRATCH = this;
    }

    onDestroy () {
        this.endScrape();
    }

    init() {
        this.getinitNum();
        this.node.on(cc.Node.EventType.TOUCH_START, this._onTouchBegin, this);
        this.node.on(cc.Node.EventType.TOUCH_MOVE, this._onTouchMoved, this);
        this.node.on(cc.Node.EventType.TOUCH_END, this._onTouchEnd, this);
        this.node.on(cc.Node.EventType.TOUCH_CANCEL, this._onTouchCancel, this);
    }

    /**
     * 结束刮刮卡的动作监听
     */
    endScrape(){
        this.node.off(cc.Node.EventType.TOUCH_START, this._onTouchBegin, this);
        this.node.off(cc.Node.EventType.TOUCH_MOVE, this._onTouchMoved, this);
        this.node.off(cc.Node.EventType.TOUCH_END, this._onTouchEnd, this);
        this.node.off(cc.Node.EventType.TOUCH_CANCEL, this._onTouchCancel, this);
    }

    /**
     * 获取初始值
     */
    getinitNum() {
        this.pixelNum = 0;
        // 记录要擦除多少像素点才能完成擦除操作
        this.achieveNum = this.scrapteArea * this.initPixel();
    }

    /**
     * 根据所设计的接触点的大小设置出一个点矩阵,在根据接触点与点矩阵的
     * 距离判断当前已经刮开了图像的多少了
     */
    initPixel() {
        let maskNode = this.mask.node.children[0];
        let x = maskNode.width, y = maskNode.height;

        //根据接触点的大小来设置矩阵点多少以及点之间的距离
        let zx = x / 2, zy = y / 2, dx = -zx, dy = -zy, dy1 = dy;
        let pixelPiont = [];
        let rx = this.scrapteRadiusX * 2;
        let ry = this.scrapteRadiusY * 2;

        // 开始排列矩阵
        for (; dx <= zx; dx += rx) {
            for (dy = dy1; dy <= zy; dy += ry) {
                let p = [dx, dy];
                // 该属性确保每个矩阵点只能有一次的检测操作
                // 被检测完后变为false不能被二次检测
                p.isTouch = true;
                pixelPiont.push(p);
            }
        }
        this.pixelPiont = pixelPiont;
        //返回像素点的个数
        return pixelPiont.length;
    }

        /**
     * 点击开始
     * @param {*} event 
     */
    _onTouchBegin (event) {
        //cc.log('touchBegin');
        this.comFun(event);
    }

    _onTouchMoved (event) {
        this.comFun(event);
    }

    _onTouchEnd (event) {
        //cc.log('touchEnd');
        this.checkScrape();
        this.comFun(event);
    }

    _onTouchCancel (event) {
        cc.log('TouchCancel')
        this.checkScrape();
    }

    comFun(event) {
        let point = event.touch.getLocation();
        point = this.mask.node.convertToNodeSpaceAR(point);
        this.checkPixelPiont(point);
        this._addCircle(point);
    }

    /**
     * 检查是否完成擦除
     */
    checkScrape() {
        cc.log(`已经刮开: ${this.pixelNum}/${this.achieveNum}`);
        if (this.achieveNum < this.pixelNum) {
            cc.log("[已经刮完图层]");
            this.achieveScrape();
        }
    }

    /**
     * 检查是否画在新的像素点上,若是话在了新的点上,接使刮开数执行++操作
     */
    checkPixelPiont(point) {
        let pixelPiont = this.pixelPiont;
        let x, y;
        for (var i in pixelPiont) {
            x = Math.abs(point.x - pixelPiont[i][0]);
            y = Math.abs(point.y - pixelPiont[i][1]);
            if (x <= this.scrapteRadiusX * 2 && y <= this.scrapteRadiusY * 2 && pixelPiont[i].isTouch) {
                // 让该元素不可以响应触摸功能
                pixelPiont[i].isTouch = false;
                this.pixelNum++;
                return;
            }
        }
    }

    /**
     * 完成刮蹭事件
     */
    achieveScrape() {
        this.endScrape();
        // 执行渐隐效果，卡片消失
        this.mask.node.runAction(cc.fadeOut(0.5));
        this.scheduleOnce((() => {
            // 执行回调事件
            cc.Component.EventHandler.emitEvents(this.scrapeEvents, new cc.Event.EventCustom('scrapeEvents'));
            //this.node.destroy();
        }).bind(this), 0.6);
    }

    /**
     * 进行刮开的操作
     * @param {*} point 
     */
    _addCircle (point) {
        var graphics = this.mask._graphics;
        //cc.log("xxxx:",graphics)
        var color = cc.color(0, 0, 0, 255);
        // 绘制椭圆
        graphics.ellipse(point.x, point.y, this.scrapteRadiusX * 2, this.scrapteRadiusY * 2);
        graphics.lineWidth = 2;
        // 填充颜色为透明
        graphics.fillColor = color;
        // 开始填充颜色
        graphics.fill();
    }

    /*
    start () {
        let self = this;
        this.node.on(cc.Node.EventType.TOUCH_START,(event)=>{
            let point = event.touch.getLocation();
            point = self.mask.node.convertToNodeSpaceAR(point);
            self._addCircle(point);
        },this);
        this.node.on(cc.Node.EventType.TOUCH_MOVE,(event)=>{
            let point = event.touch.getLocation();
            point = self.mask.node.convertToNodeSpaceAR(point);
            self._addCircle(point);
        },this);
        
    }

    _addCircle(pos : cc.Vec2) {
        let _graphics = this.mask._graphics;
        cc.log(_graphics);
        _graphics.lineWidth = 1;
        _graphics.strokeColor = cc.color(255,0,0);
        _graphics.fillColor = cc.color(0,255,0);
        _graphics.circle (pos.x,pos.y,this.size);
        _graphics.fill();
        _graphics.stroke();
    }*/
}
