//version1.01

const {ccclass, property} = cc._decorator;

@ccclass
export default class HotUpdate extends cc.Component {

    @property({type: cc.Asset})
    manifestUrl : cc.Asset = null;

    @property
    _updating : boolean = false;

    @property
    _canRetry : boolean = false;

    @property
    _storagePath : string = "";

    @property
    _updateListener : any = null;

    @property
    _am : any = null;

    @property
    _failCount : number = 0;

    @property(cc.Node)
    wrapper : cc.Node = null;

    @property (cc.Label)
    countLabel : cc.Label = null;

    @property (cc.Sprite)
    progressBar : cc.Sprite = null;

    @property
    versionCompareHandle : Function = null;

    @property
    remoteVersion : string = "";

    @property
    _localVersion : string = "";

    onLoad(){
        //(window as any).HOTUPDATE = this;
    }

    start() {
        try{
            //正在审核
            //if (GAME_CONFIG.baseConfig.clientMode === 1){
            //    return;
            //}
            // Hot update is only available in Native build
            if (!cc.sys.isNative) {
                cc.log("Hot update is only available in Native build ,now is not ");
                return;
            }
            //游戏的localVersion，用于大更新时启用不同的update文件夹，避免旧缓存影响新的安装包
            this._localVersion = "105";
            this.updateResource();

            //动态更新游戏的热更地址
            //this.handleManifestFile(GAME_CONFIG.baseConfig.hotUpdateUrl);
            this.handleManifestFile("http://127.0.0.1:3000/hotupdate/ios/");
            

            this.checkUpdate();
        } catch(e) {
            cc.log(e);
        }
    }

    updateResource () {
        this._storagePath = ((jsb.fileUtils ? jsb.fileUtils.getWritablePath() : '/') + 'update' + this._localVersion);   //获取本地缓存目录
        cc.log('[updateResource] Storage path for remote asset : ' + this._storagePath);


        // Setup your own version compare handler, versionA and B is versions in string
        // if the return value greater than 0, versionA is greater than B,
        // if the return value equals 0, versionA equals to B,
        // if the return value smaller than 0, versionA is smaller than B.
        // versionA: local versionB: remote
        let _this = this;
        this.versionCompareHandle = function (versionA, versionB) {
            SETTINGS.hotUpdateVersion = versionA;
            SETTINGS.save();
            _this.remoteVersion = versionB;
            cc.log("JS Custom Version Compare: version A is " + versionA + ', version B is ' + versionB);
            let vA = versionA.split('.');
            let vB = versionB.split('.');
            for (let i = 0; i < vA.length; ++i) {
                let a = parseInt(vA[i]);
                let b = parseInt(vB[i] || 0);
                if (a === b) {
                    continue;
                }
                else {
                    return a - b;
                }
            }
            if (vB.length > vA.length) {
                return -1;
            }
            else {
                return 0;
            }
        };

        // Init with empty manifest url for testing custom manifest
        this._am  = new jsb.AssetsManager('', this._storagePath, this.versionCompareHandle);

        // Setup the verification callback, but we don't have md5 check function yet, so only print some message
        // Return true if the verification passed, otherwise return false
        this._am.setVerifyCallback(function (path, asset) {
            // When asset is compressed, we don't need to check its md5, because zip file have been deleted.
            let compressed = asset.compressed;
            // Retrieve the correct md5 value.
            let expectedMD5 = asset.md5;
            // asset.path is relative path and path is absolute.
            let relativePath = asset.path;
            // The size of asset file, but this value could be absent.
            let size = asset.size;
            if (compressed) {
                cc.log("Verification passed : " + relativePath);
                return true;
            }
            else {
                cc.log("Verification passed : " + relativePath + ' (' + expectedMD5 + ')');
                return true;
            }
        });

        cc.log('[HotUpdate] is ready, please check or directly update.');

        if (cc.sys.os === cc.sys.OS_ANDROID) {
            // Some Android device may slow down the download process when concurrent tasks is too much.
            // The value may not be accurate, please do more test and find what's most suitable for your game.
            this._am.setMaxConcurrentTask(2);
            cc.log("Max concurrent tasks count have been limited to 2");
        }
    }

    handleManifestFile(newUrl : string){
        cc.log("[HOTUPDATE] newUrl: " + newUrl);
        if (this._am.getState() === jsb.AssetsManager.State.UNINITED) {
            //优先查找缓存里的manifest，如果没有则启用关联的project.manifest
            let manifestString : string = jsb.fileUtils.getStringFromFile(this._storagePath + "/project.manifest");
            if(manifestString.length === 0){
                manifestString = jsb.fileUtils.getStringFromFile(this.manifestUrl.nativeUrl);
                //cc.log("manifestString: ", manifestString);
            }
            //动态修改热更新的地址为newUrl
            let obj = JSON.parse(manifestString);
            obj.packageUrl = newUrl;
            obj.remoteManifestUrl = newUrl + "project.manifest";
            obj.remoteVersionUrl  = newUrl + "version.manifest";
            let afterString = JSON.stringify(obj);
            cc.log("remoteVersionUrl: ", obj.remoteVersionUrl);
            let manifest = new jsb.Manifest(afterString, this._storagePath);
            this._am.loadLocalManifest(manifest, this._storagePath);
        }
    }

    checkCb (event) {
        cc.log('Code: ' + event.getEventCode());
        switch (event.getEventCode())
        {
            case jsb.EventAssetsManager.ERROR_NO_LOCAL_MANIFEST:
                cc.log( "No local manifest file found, hot update skipped.");
                this.wrapper.active = false;
                break;
            case jsb.EventAssetsManager.ERROR_DOWNLOAD_MANIFEST:
            case jsb.EventAssetsManager.ERROR_PARSE_MANIFEST:
                cc.log("Fail to download manifest file, hot update skipped.");
                this.wrapper.active = false;
                break;
            case jsb.EventAssetsManager.ALREADY_UP_TO_DATE:
                cc.log("Already up to date with the latest remote version.");
                this.wrapper.active = false;
                break;
            case jsb.EventAssetsManager.NEW_VERSION_FOUND:
                cc.log('New version found, please try to update.');
                this.progressBar.fillRange = 0;
                break;
            default:
                return;
        }
        
        this._am.setEventCallback(null);
        //this._checkListener = null;
        this._updating = false;
        
        if(event.getEventCode() === jsb.EventAssetsManager.NEW_VERSION_FOUND){
            this.wrapper.active = true;
            this.hotUpdate();
        }
    }

    updateCb (event) {
        let needRestart = false;
        let failed = false;
        switch (event.getEventCode())
        {
            case jsb.EventAssetsManager.ERROR_NO_LOCAL_MANIFEST:
                cc.log('No local manifest file found, hot update skipped.');
                failed = true;
                break;
            case jsb.EventAssetsManager.UPDATE_PROGRESSION:
                let totalFiles = event.getTotalFiles();
                let downloadedFiles = event.getDownloadedFiles();
                if(totalFiles >= 0 && downloadedFiles > 0){
                    let progress = downloadedFiles / totalFiles;
                    this.countLabel.string = (progress * 100.00).toFixed(2) + "%" + ` (${downloadedFiles}/${totalFiles})`;
                    this.progressBar.fillRange = progress;
                } else {
                    this.countLabel.string = "0%";
                }

                let msg = event.getMessage();
                if (msg) {
                    cc.log('Updated file: ' + msg);
                }
                break;
            case jsb.EventAssetsManager.ERROR_DOWNLOAD_MANIFEST:
            case jsb.EventAssetsManager.ERROR_PARSE_MANIFEST:
                cc.log('[ERROR_PARSE_MANIFEST] Fail to download manifest file, hot update skipped.');
                failed = true;
                break;
            case jsb.EventAssetsManager.ALREADY_UP_TO_DATE:
                cc.log('Already up to date with the latest remote version.');
                failed = true;
                break;
            case jsb.EventAssetsManager.UPDATE_FINISHED:
                cc.log('Update finished. ' + event.getMessage());
                this.countLabel.string = "100% 更新完成，遊戲重新啟動中...";
                needRestart = true;
                SETTINGS.hotUpdateVersion = this.remoteVersion;
                SETTINGS.save();
                break;
            case jsb.EventAssetsManager.UPDATE_FAILED:
                cc.log('Update failed. ' + event.getMessage());
                this._updating = false;
                this._canRetry = true;
                break;
            case jsb.EventAssetsManager.ERROR_UPDATING:
                cc.log('Asset update error: ' + event.getAssetId() + ', ' + event.getMessage());
                break;
            case jsb.EventAssetsManager.ERROR_DECOMPRESS:
                cc.log(event.getMessage());
                break;
            default:
                break;
        }

        if (failed) {
            this._am.setEventCallback(null);
            this._updateListener = null;
            this._updating = false;
            this.retry();
        }

        if (needRestart) {
            this._am.setEventCallback(null);
            this._updateListener = null;
            // Prepend the manifest's search path
            let searchPaths = jsb.fileUtils.getSearchPaths();
            let newPaths = this._am.getLocalManifest().getSearchPaths();
            cc.log("[newPaths] " + JSON.stringify(newPaths));
            Array.prototype.unshift.apply(searchPaths, newPaths);
            cc.log("[searchPaths] " + JSON.stringify(searchPaths));
            // This value will be retrieved and appended to the default search path during game startup,
            // please refer to samples/js-tests/main.js for detailed usage.
            // !!! Re-add the search paths in main.js is very important, otherwise, new scripts won't take effect.
            cc.sys.localStorage.setItem('HotUpdateSearchPaths' + this._localVersion, JSON.stringify(searchPaths));
            
            //HotUpdateSearchPaths
            jsb.fileUtils.setSearchPaths(searchPaths);

            cc.audioEngine.stopAll();
            cc.log("[hotupdate][done] restart game");
            if(sdkbox.IAP){
                cc.log(" clean sdkbox iap");
                sdkbox.IAP.setListener({});
            }
            this.scheduleOnce(()=>{
                cc.game.restart();
            }, 3);
        }
    }

    retry () {
        if (!this._updating && this._canRetry) {
            //this.panel.retryBtn.active = false;
            this._canRetry = false;
            
            cc.log('Retry failed Assets...');
            this._am.downloadFailedAssets();
        }
    }

    checkUpdate () {
        cc.log("[hotupdate] checkUpdate")
        if (this._updating) {
            cc.log('Checking or updating ...');
            return;
        }
        if (this._am.getState() === jsb.AssetsManager.State.UNINITED) {
            // Resolve md5 url
            cc.log("[hotupdate] loadLocalManifest");
            let url = this.manifestUrl.nativeUrl;
            if (cc.loader.md5Pipe) {
                url = cc.loader.md5Pipe.transformURL(url);
            }
            this._am.loadLocalManifest(url);
        }
        if (!this._am.getLocalManifest() || !this._am.getLocalManifest().isLoaded()) {
            cc.log('Failed to load local manifest ...');
            return;
        }
        this._am.setEventCallback(this.checkCb.bind(this));

        this._am.checkUpdate();
        this._updating = true;
    }

    hotUpdate () {
        cc.log("[hotUpdate] bootup");
        if (this._am !== null && !this._updating) {
            cc.log("[hotUpdate] begin");
            this._am.setEventCallback(this.updateCb.bind(this));

            if (this._am.getState() === jsb.AssetsManager.State.UNINITED) {
                // Resolve md5 url
                let url = this.manifestUrl.nativeUrl;
                if (cc.loader.md5Pipe) {
                    url = cc.loader.md5Pipe.transformURL(url);
                }
                this._am.loadLocalManifest(url);
            }

            this._failCount = 0;
            this._am.update();
            //this.panel.updateBtn.active = false;
            this._updating = true;
        } else {
            this.countLabel.string = "[hotUpdate] error";
        }
    }

    onDestroy () {
        if (this._updateListener) {
            this._am.setEventCallback(null);
            this._updateListener = null;
        }
    }
}