export default class NativeController {
    public static readonly instance = new NativeController();
    private constructor() { }
    
    public nativeClipBoard(str : string){
        if (cc.sys.isNative) {
            jsb.copyTextToClipboard(str);
        }
        /*
        if (cc.sys.isNative&&cc.sys.os==cc.sys.OS_IOS) {
            let ret = jsb.reflection.callStaticMethod("NativeController","sendCopyMessage:", str);
            cc.log(ret);
        }

        if(cc.sys.isNative && cc.sys.os==cc.sys.OS_ANDROID){
            jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "setClipboardData", "(Ljava/lang/String;)V", str);
        }*/
    }

    public nativeGetDeviceId(){
        if (cc.sys.isNative && cc.sys.os==cc.sys.OS_IOS) {
            let ret = jsb.reflection.callStaticMethod("NativeController","getIdfa");
            cc.log(ret);
            return ret;
        }

        if(cc.sys.isNative && cc.sys.os==cc.sys.OS_ANDROID){
            let deviceid = jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "getDeviceId", "()Ljava/lang/String;");
            cc.log(deviceid);
            return deviceid;
        }

        return "";
    }

    public nativeGetAppId(){
        if (cc.sys.isNative&&cc.sys.os==cc.sys.OS_IOS) {
            let ret = jsb.reflection.callStaticMethod("NativeController","getAppId");
            cc.log(ret);
            return ret;
        }

        if(cc.sys.isNative && cc.sys.os==cc.sys.OS_ANDROID){
            let ret = jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "getAppid", "()Ljava/lang/String;");
            cc.log(ret);
            return ret;
        }

        return "";
    }

    public keepScreenOn(){
        //cc.Devicel里面已有实现
        //if (cc.sys.isNative&&cc.sys.os==cc.sys.OS_IOS) {
        //    let ret = jsb.reflection.callStaticMethod("NativeController","keepScreenOn");
        //    cc.log("[keepScreenOn]");
        //    return ret;
        //}
        //return;

        if (cc.sys.isNative) {
            jsb.Device.setKeepScreenOn(true);
            //cc['Device'].setKeepScreenOn(true)
        }
    }

    public saveToPhotoAlbum(imgPath : string){
        if (cc.sys.isNative && cc.sys.os==cc.sys.OS_IOS) {
            let ret = jsb.reflection.callStaticMethod("NativeController", "saveToPhotoAlbum:", imgPath);
            cc.log("[Native] saveToPhotoAlbum");
            return ret;
        }

        if(cc.sys.isNative && cc.sys.os==cc.sys.OS_ANDROID){
            jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "saveToPhotoAlbum", "(Ljava/lang/String;)V", imgPath);
            cc.log("[Native] saveToPhotoAlbum");
        }
    }

    public selectPhotoFromAlbum(){
        if (cc.sys.isNative && cc.sys.os==cc.sys.OS_IOS) {
            let ret = jsb.reflection.callStaticMethod("NativeController", "openPhoto");
            cc.log("[Native] selectPhotoFromAlbum");
            return ret;
        }
        
    }

    public cropPicturesCallback(purpose : string, url : string){
        cc.log(`[cropPicturesCallback] purpose: ${purpose} url: ${url}`);
    }

}