const {ccclass, property} = cc._decorator;


@ccclass
export default class NewClass extends cc.Component {
    @property(cc.Label)
    iapInfo : cc.Label = null;

    onLoad(){
        this.init();
    }

    init(){
        cc.log("[IAP] init");
        if(cc.sys.isNative){
            let _this = this;
            sdkbox.IAP.setDebug(true);
            sdkbox.IAP.setListener({
                onInitialized : (success) => {
                    //this.initLoadingNode.active = false;
                    cc.log("[onInitialized]", JSON.stringify(success));
                },
                onSuccess : (p) => {
                    let _this = this;
                    cc.log("[onSuccess]", JSON.stringify(p));
                },
                onFailure : (p, msg) => {
                    cc.log("[onFailure]");
                    cc.log(JSON.stringify(p), msg);
                    cc.log("購買失敗");
                },
                onCanceled: (p) => {
                    cc.log("[onCanceled]", JSON.stringify(p));
                    cc.log("購買失敗");
                },
                onRestored : (p) => {
                    cc.log("[onRestored]",JSON.stringify(p));
                },
                onProductRequestSuccess : (products) => {
                    this.iapInfo.string = JSON.stringify(products);
                    cc.log("[onProductRequestSuccess]", JSON.stringify(products));
                },
                onProductRequestFailure : (msg) => {
                    cc.log("購買失敗 " + JSON.stringify(msg));
                    cc.log("[onProductRequestFailure]", JSON.stringify(msg));
                }
            });

            //sdkbox.IAP.init(_config);
            cc.log("[sdkbox] init");
            if(sdkbox.IAP) sdkbox.IAP.init();
            //this.scheduleOnce(()=>{
            //    sdkbox.IAP.refresh();  //触发onProductRequestSuccess，打印所有产品信息
            //},3);
            
        }
    }

    purchase(event, rawData : string){
        cc.log("[IAP] purchaseing ", rawData);
        sdkbox.IAP.purchase(rawData);
    }

}
